body, html {
    height: 100vh;
    margin: 0;
}

.skillscontainer{
    background-color: #F2DFA7;
    /* max-width: 1280px; */
    padding: 40px 180px;
}

.title2{
    text-align: center;
    color: #28594B;
}

.skillset{
    padding: 10px;
}




.item{
    display: inline-block;
}

.star{
    /* padding: 5px; */
    display: inline-block;
    transition: 0.3s;
}

.itemcontainerpersonal{
    transition: 0.3s;
    padding: 10px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.itemcontainerpersonal:hover {
    background-color: #e7d6a1;
}

.itemcontainerpersonal:hover .starpersonal{
    transform: scale(1.1);
}
