.navigators {
    float: right;
    position: -webkit-sticky;
    position: sticky;
    top: 50px;
    right: 8px;
    padding: 0px;
    /* border: 2px solid red; */
}

/* ACTIVE FOR DESKTOP MODE ONLY */
@media only screen and (min-width: 1000px){
    .mobile{
        display: none;
        /* general in desktop mode */
    }

    .personaldetails {
        display: inline-block;
        padding: 20px;
    }

    .tumlogo{
        vertical-align: middle;
        height: 35px;
    }

    .itblogo{
        vertical-align: middle;
        height: 60px;
    }

    .ifxlogo{
        vertical-align: middle;
        height: 45px;
    }

    .keihinlogo{
        vertical-align: middle;
        height: 30px;
    }
    
    .itemcontainerleft{
        transition: 0.3s;
        padding: 10px;
        width: 50%;
        display: flex;
        justify-content: space-between;
    }
    
    .itemcontainerright{
        transition: 0.3s;
        padding: 10px;
        width: 50%;
        display: flex;
        justify-content: space-between;
    }
    
    .itemcontainerleft:hover{
        background-color: #e7d6a1;
    }
    
    .itemcontainerleft:hover .star{
        transform: scale(1.1);
    }
    
    .itemcontainerright:hover{
        background-color: #e7d6a1;
    }
    
    .itemcontainerright:hover .star{
        transform: scale(1.1);
    }

    .row{
        display: flex;
        justify-content: space-between;
    }

    .projecttext{
        /* display: flex;
        align-items: center; */
        text-align: center;
        color: white;
        vertical-align: middle;
        width: fit-content;
        margin: auto;
        transition: 0.07s;
        border: 0px solid white;
        padding: 10px;
        transform: translateY(240px);
    }
    
    .projectcontainerdetail:hover .projecttext{
        border: 2px solid white;
        padding: 10px;
    }

    .project1{
        background-image: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) ), url('./components/projects/bdb.jpg');
        background-size: 100%;
        background-repeat: no-repeat;
    }
    
    .project1:hover{
        background-image: linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url('./components/projects/bdb.jpg');
        background-size: 120%;
        background-repeat: no-repeat;
    }
    
    .project2{
        background-image: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) ), url('./components/projects/tableau.jpg');
        background-size: 100%;
        background-repeat: no-repeat;
    }
    
    .project2:hover{
        background-image: linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url('./components/projects/tableau.jpg');
        background-size: 120%;
        background-repeat: no-repeat;
    }
    
    .project3{
        background-image: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) ), url('./components/projects/muctok.jpg');
        background-size: 100%;
        background-repeat: no-repeat;
    }
    
    .project3:hover{
        background-image: linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url('./components/projects/muctok.jpg');
        background-size: 120%;
        background-repeat: no-repeat;
    }

    .contactcontainer{
        background-color: #8C6542;
        /* max-width: 1280px; */
        padding: 40px 180px 600px;
    }
    
}


/* ACTIVE FOR MOBILE MODE ONLY */
@media only screen and (max-width: 1000px){
    .desktop{
        display: none;
        /* general in mobile mode here Patrick, Home, Contact Us, Sign In will disappear because of desktop class */
    }

    /* make get started smaller in mobile */
    html{
        font-size: 12px;
    }

    .textdesktopbig{
        font-size: 12px;
    }
    /* big in desktop, small in mobile */

    .hometoprofilemobile{
        display: block;
        position: absolute;
        bottom: 0%;
        width: 100%;
        right: 0px;
        padding: 0px;
        background-color: white;
        text-align: right;
        opacity: 55%;
        /* border-radius: 70%; */
    }

    .container1mobile{
        padding: 30px 10px;
        display: absolute;
        background-color: #F2F2F2;
    }

    .container2mobile{
        padding: 30px 10px;
        display: absolute;
        background-color: #F2DFA7;
    }

    .contactcontainermobile{
        padding: 30px 10px 500px;
        display: absolute;
        background-color: #8C6542;
    }

    .personaldetailsmobile {
        display: block;
    }

    .profilepicturemobile1 {
        width: 100%;
        text-align: center;
        display: block;
        align-items: center;
        justify-content: center;
    }

    .profilepicturemobile2 {
        height: 180px;
        width: 140px;
        /* margin-left: auto; */
        /* border-radius: 50%; */
        border: 2px solid #40352C;
        text-align: center;
        /* display: block; */
        align-items: center;
        justify-content: center;
    }

    .instlogomobile{
        vertical-align: middle;
        height: 25px;
    }

    .itemcontainermobile{
        transition: 0.3s;
        padding: 10px;
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    
    .itemcontainermobile:hover{
        background-color: #e7d6a1;
    }
    
    .itemcontainermobile:hover .star{
        transform: scale(1.1);
    }
    
    .rowmobile{
        display: block;
    }

    .projecttextmobile{
        text-align: center;
        color: white;
        vertical-align: middle;
        width: fit-content;
        margin: auto;
        transition: 0.07s;
        border: 2px solid white;
        padding: 10px;
        transform: translateY(240px);
    }

    .project1mobile{
        background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url('./components/projects/bdb.jpg');
        background-size: cover;
        background-repeat: no-repeat;
    }
    
    .project2mobile{
        background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url('./components/projects/tableau.jpg');
        background-size: cover;
        background-repeat: no-repeat;
    }

    .project3mobile{
        background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url('./components/projects/muctok.jpg');
        background-size: cover;
        background-repeat: no-repeat;
    }

    .appbar1{
        position: -webkit-sticky;
        position: sticky;
        top: 0px;
        width: 100%;
        display: flex;
    }

    .appbar2{
        list-style: none;
        display: flex;
        position: -webkit-sticky;
        position: sticky;
    }


}