.naviall {
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    /* right: 5%; */
    /* border: 2px solid red; */
}

.containernav {
    list-style: none;
    float: right;
    top: 0px;
    margin-top: 0px;
    /* position: sticky; */
}

.singlenav {
    display: block;
    margin-left: 0px;
    padding: 2px;
    /* background-color: #455931; */
    background-color: #302F40;
    text-align: left;
    transition: 0.3s;
    /* border: 1px solid red; */
}

.singlenav:hover {
    display: block;
    margin-left: 0px;
    padding: 2px;
    /* background-color: #455931; */
    background-color: #52506c;
    text-align: left;
    /* transform: scale(1.05); */
    /* border: 1px solid red; */
}

.singlenavactive {
    display: block;
    margin-left: 0px;
    padding: 2px;
    /* background-color: #455931; */
    background-color: #47A603;
    text-align: left;
    transition: 0.3s;
    /* border: 1px solid red; */
}

.singlenavactive:hover {
    display: block;
    margin-left: 0px;
    padding: 2px;
    /* background-color: #455931; */
    background-color: #53c005;
    text-align: left;
    /* transform: scale(1.05); */
}

