body, html {
    height: 100vh;
    margin: 0;
}

.headerall{
  background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url('./DSC03300.JPG');
  height: 100vh; 
  background-repeat: no-repeat; 
  background-size: cover; 
  background-position: center; 
  background-attachment: scroll; 
  /* border: 2px solid red; */
}

.container{
    position: relative;
    border: none;
    vertical-align: middle;
    height: 100%;
    /* display: inline-block; */
    text-align: center;
    /* border: 2px solid red; */
}

.headertext1{
    text-align: center;
    padding-bottom: 20px;
    border-bottom: 1px solid white;
    margin-top: 50vh;
    vertical-align: middle;
    display: inline-block;
}

.headertext2{
    text-align: center;
    font-size: 18px;
    display: block;
}

.hometoprofile{
    display: block;
    position: absolute;
    bottom: 0%;
    right: 16px;
    /* height: 56px; */
    width: 117px;
    padding: 10px;
    background-color: white;
    text-align: right;
    opacity: 55%;
    /* border: 2px solid red; */
}

@keyframes movement1{
    /* dancing */
    0%{
        transform: translate(0,0);
    }
    50%{
        transform: translate(0px,8px);
    }  
    100%{
        transform: translate(0,0);
    }
}

.updown{
    animation: movement1 1.2s infinite;
}